'use client'
import {useState} from "react"
import ListProducts from "./components/listProducts/"
import FormSearchProducts from "./components/formSearchProducts/"
import {SacoUi} from "./ui/SacoUi"




const ListSelectedProducts = ({ selectedProducts, Card, onQuantityChange }) => {
    const listContainerStyle = {
      display: 'flex',
      flexDirection: 'column',
      gap: '1rem',
      marginTop: '2rem',
      paddingTop: '1rem',
      borderTop: '1px solid #eee'
    };

    const productItemStyle = {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'space-between',
        gap: '1rem'
    };

    const selectedProductArray = Object.values(selectedProducts).filter(p => p.quantity > 0);

    if (selectedProductArray.length === 0) {
        return null; // No mostrar nada si no hay productos seleccionados
    }

    return (
        <div>
            <h2>Productos Seleccionados</h2>
            <div style={listContainerStyle}>
                {selectedProductArray.map(({ product, quantity }) => (
                    <div key={product.nombre} style={productItemStyle}>
                        <div style={{flexGrow: 1}}>
                            <Card product={product} />
                        </div>
                        <ButtonAdd 
                            product={product} 
                            onQuantityChange={onQuantityChange} 
                            quantity={quantity}
                        />
                    </div>
                ))}
            </div>
        </div>
    );
};

export default function Home() {

  const [products, setProducts] = useState([
    {nombre: "whiskas", peso: 10, precioPublico: 1000 },
    {nombre: "pedigree", peso: 15, precioPublico: 1200 },
    {nombre: "dog chow", peso: 20, precioPublico: 1500 }
  ]);

  const [selectedProducts, setSelectedProducts] = useState({});

  const handleQuantityChange = (product, newQuantity) => {
      const quantity = Math.max(0, newQuantity); // Asegurarse de que la cantidad no sea negativa
      setSelectedProducts(prevSelected => ({
          ...prevSelected,
          [product.nombre]: { product, quantity }
      }));
  };

  return (
    <div style={{
      width:"100vw",
      minHeight:"100vh",
      display:"flex",
      flexDirection:"column",
      padding:"1rem"
    }}>
      <div style={{flexGrow:1, overflowY:"auto", padding: "0 1rem"}}>
           <h1>Productos Disponibles</h1>
           <ListProducts 
            products={products} 
            Card={SacoUi} 
            onQuantityChange={handleQuantityChange} 
            selectedProducts={selectedProducts}
           />
           <ListSelectedProducts 
            selectedProducts={selectedProducts} 
            Card={SacoUi} 
            onQuantityChange={handleQuantityChange}
           />
      </div>
      <div style={{padding:"1rem"}}>
       <FormSearchProducts />
      </div>
    </div>
  );
}
